<template>
  <v-container>
    <!-- Removed search input and button -->
    
    <v-alert v-if="loading" type="info" dismissible>
      Loading...
    </v-alert>
    
    <v-alert v-if="error" type="error" dismissible>
      An error occurred: {{ error.message }}
    </v-alert>

    <v-data-table
      v-if="!loading && !error && uniqueProjects.length"
      :headers="headers"
      :items="uniqueProjects"
      item-key="project"
      class="elevation-1"
    >
    <template v-slot:[`item.source`]="{ item }">
      {{ item.source }}
    </template>
      <template v-slot:[`item.project`]="{ item }">
        {{ item.project }}
      </template>
     
    </v-data-table>
  </v-container>
</template>

<script>
import gql from 'graphql-tag';
import { ref } from 'vue';
import { useQuery } from '@vue/apollo-composable';

export default {
  name: 'listProject',
  setup() {
    const uniqueProjects = ref([]);
    const loading = ref(false);
    const error = ref(null);

    const headers = [
      { text: 'Source', value: 'source' },
      { text: 'Project', value: 'project' },
  
    ];

    const GET_ALL_PROJECTS = gql`
      query {
        leads(pagination: { limit: 1000 }) {
          data {
            attributes {
              project
              source
            }
          }
        }
      }
    `;

    const fetchProjects = async () => {
      loading.value = true;
      error.value = null;

      try {
        const { result, error: queryError } = useQuery(GET_ALL_PROJECTS);

        if (queryError.value) {
          error.value = queryError.value;
          console.error('Error fetching projects:', error.value);
        } else if (result.value) {
          const allLeads = result.value.leads.data;
          uniqueProjects.value = extractUniqueProjects(allLeads);
        }
      } catch (e) {
        error.value = e;
        console.error('Unexpected error:', e);
      } finally {
        loading.value = false;
      }
    };

    const extractUniqueProjects = (leads) => {
      const uniqueProjectsMap = new Map();
      leads.forEach(lead => {
        const project = lead.attributes.project;
        const source = lead.attributes.source;
        if (!uniqueProjectsMap.has(project)) {
          uniqueProjectsMap.set(project, { project, source });
        }
      });
      return Array.from(uniqueProjectsMap.values());
    };

    // Fetch all projects when component is created
    fetchProjects();

    return {
      uniqueProjects,
      loading,
      error,
      headers,
    };
  },
};
</script>

<style scoped>
/* Add any additional styling if needed */
</style>
